<template>
  <div class="memberCenter">
    <a-form layout="inline">
      <a-form-item>
        <a-input
          style="width: 300px"
          placeholder="可通过手机号进行搜索"
          v-model.trim="telephone"
          allowClear
        ></a-input>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" @click="search(false)">搜索</a-button>
      </a-form-item>
    </a-form>
    <a-table
        bordered
        :dataSource="tableData"
        :columns="tableColumns"
        :loading="tableLoading"
        :pagination="false"
        style="margin: 15px 0"
        :scroll="{ x: tableScrollWidth, y: tableScrollHeight }"
        size="small"
      >
        <template slot-scope="text, record" slot="operation">
          <editProxy
            :itemData="record"
            :getTableData="getTableData"
          ></editProxy>
          <a-button
            type="primary"
            size="small"
            @click="goExpressConfig(record)"
            style="margin-left: 15px"
            >快递配置</a-button
          >
        </template>
      </a-table>
      <!-- 分页 -->
      <pagination :pagination="pagination" />
  </div>
</template>
<script>
// 常量
import constant from "@/constant/memberCenter.js";
import list from "@/common/json/list.js";
// mixins
import paginationMixins from "@/common/mixins/pagination.js";
// 分页组件
import pagination from "@/components/pagination.vue";
export default {
  name: "memberCenter",
  components: { pagination },
  mixins: [paginationMixins],
  data() {
    return {
      telephone: '',
      tableData: [], //表格数据
      tableColumns: constant.pageTableColumns, //表格表头
      tableLoading: false, //表格loading
    }
  },
  mounted() {
    this.getTableData(false);
  },
  computed: {
    //表格宽度
    tableScrollWidth() {
      let length = 0;
      for (let j = 0; j < this.tableColumns.length; j++) {
        length += this.tableColumns[j].width;
      }
      return length;
    },
    // 表格高度
    tableScrollHeight() {
      let clientHeight =
        document.body.clientHeight || document.documentElement.clientHeight;
      return clientHeight - 340;
    },
  },
  methods: {
    // 获取表格数据
    getTableData(paginationFlag) {
      let vm = this;
      let params = {telephone: this.telephone};
      params.page_size = this.pagination.page_size;
      if (paginationFlag) {
        params.page = this.pagination.current_page;
      }
      this.tableLoading = true;
      this.ProxyApi.proxyList(params).then((res) => {
        vm.tableLoading = false;
        if (res.status == 200) {
          let arr = res.data.list;
          for (let i = 0; i < arr.length; i++) {
            const element = arr[i];
            element.key = (i + 1).toString();
            element.statusHtml = element.status == "1";
          }
          vm.tableData = arr;
          const pagination = { ...vm.pagination };
          pagination.totals = res.data.total;
          if (!paginationFlag) {
            pagination.current_page = 1;
          }
          vm.pagination = pagination;
        }
      });
    },
  }
}
</script>
<style lang="scss" scoped='scoped'>
  .memberCenter {
    background-color: #ffffff;
  }
</style>