<!--
 * @Description: 分页组件
 * @Autor: WangDebiao
 * @Date: 2020-06-03 14:59:22
--> 
<template>
  <div class="paginationPage">
    <a-row type="flex" justify="end" align="middle">
      <span class="totalsBox" v-if="pagination.showTotalsDesc">
        {{pagination.totalsDesc.leftWord}}
        <span class="totalsNum">&nbsp;{{pagination.totals}}&nbsp;</span>
        {{pagination.totalsDesc.rightWord}}
      </span>
      <a-pagination
        v-model="pagination.current_page"
        :page-size-options="pageSizeOptions"
        :total="pagination.totals"
        style="display:inline-flex;"
        :show-size-changer="showSizeChanger"
        :page-size="pagination.page_size"
        @change="currentChange"
        @showSizeChange="pageSizeChange"
      ></a-pagination>
    </a-row>
  </div>
</template>
<script>
export default {
  name: "paginationPage",
  props: {
    pagination: {
      default: {},
    },
    showSizeChanger: {
      default: true,
    },
    pageSizeOptions: {
      type: Array,
      default: () => ["50", "100", "200", "500"],
    },
  },
  data() {
    return {};
  },
  methods: {
    // 点击页码
    currentChange(page) {
      const pagination = { ...this.pagination };
      pagination.current_page = page;
      this.reRenderTable(pagination, true);
    },
    // 切换每页数量
    pageSizeChange(current, size) {
      const pagination = { ...this.pagination };
      pagination.current_page = 1;
      pagination.page_size = size;
      this.reRenderTable(pagination, false);
    },
    // 更新表格数据
    reRenderTable(pagination, flag) {
      this.$parent[this.pagination.changePaginationName](pagination, flag);
    },
  },
};
</script>
<style lang="scss" scoped>
.paginationPage {
  .totalsBox {
    margin-right: 15px;
    line-height: 32px;
    .totalsNum {
      color: #1890ff;
    }
  }
}
</style>